import { createApp } from 'vue'
import App from './App.vue'
import router from './router';
import axios from 'axios';
import mitt from 'mitt';

import { IonicVue } from '@ionic/vue';

/* Core CSS required for Ionic components to work properly */
import '@ionic/vue/css/core.css';

/* Basic CSS for apps built with Ionic */
import '@ionic/vue/css/normalize.css';
import '@ionic/vue/css/structure.css';
import '@ionic/vue/css/typography.css';

/* Optional CSS utils that can be commented out */
import '@ionic/vue/css/padding.css';
import '@ionic/vue/css/float-elements.css';
import '@ionic/vue/css/text-alignment.css';
import '@ionic/vue/css/text-transformation.css';
import '@ionic/vue/css/flex-utils.css';
import '@ionic/vue/css/display.css';

/* Theme variables */
import './theme/variables.css';

import store from './store/index';

const eventBus = mitt();

axios.interceptors.response.use(function (response) {
  return response
}, function (error) {
  if ((error.response.status === 401 || error.response.status === 403) && error.request.responseURL.indexOf('?password') == -1  && error.request.responseURL.indexOf('quizmanagement/quiz/') == -1) {
    window.localStorage.removeItem('loggedInJwt');
    window.localStorage.removeItem('teacherJwt');
    window.location.href = "/tabs/login";
  }
  return Promise.reject(error)
})

axios.interceptors.request.use(function (request) {
  request.headers.Authorization = 'Bearer '+window.localStorage.getItem('teacherJwt');  
  
  return request;
})

const app = createApp(App)
  .use(IonicVue)
  .use(router)
  .use(store);
router.isReady().then(() => {
  app.mount('#app');
});

app.config.globalProperties.eventBus = eventBus;