const state = () => ({
    jwt: "",
    loggedIn: false,
    teacherJwt: "",
});
 
const getters = {
    jwt(state){
        return state.jwt;
    },
    teacherJwt(state){
        return state.teacherJwt;
    },
    loggedIn(state){
        return state.loggedIn || window.localStorage.getItem('loggedInJwt') || window.localStorage.getItem('teacherJwt');
    }
};
 
const actions = {
    doLogin({commit}, payload){
        commit('doLogin', payload);
    },
    doTeacherLogin({commit}, payload){
        commit('doTeacherLogin', payload);
    },
    doLogout({commit}){
        commit('doLogout');
    }
};
 
const mutations = {
    doLogin(state, payload){
        state.jwt = payload;
        state.loggedIn = true;
        window.localStorage.setItem('loggedInJwt', payload);
    },
    doTeacherLogin(state, payload){
        state.teacherJwt = payload.token;
        state.loggedIn = true;
        window.localStorage.setItem('teacherJwt', payload.token);
    },
    doLogout(state){
        console.log("here?")
        state.jwt = "";
        state.teacherJwt = "";
        state.loggedIn = false;
        window.localStorage.removeItem('loggedInJwt');
        window.localStorage.removeItem('teacherJwt');
    }
};
 
export default{
    namespaced: true,
    state,
    getters,
    actions,
    mutations
}