<template>
  <ion-page>
    <ion-tabs>
      <ion-tab-bar slot="bottom">
        <ion-tab-button tab="resources" href="/tabs/resources">
          <ion-icon :icon="triangle" />
          <ion-label>View resources</ion-label>
        </ion-tab-button>
        <ion-tab-button tab="resourceCreate" href="/tabs/newresource" v-if="loggedIn()">
          <ion-icon :icon="triangle" />
          <ion-label>Create resource</ion-label>
        </ion-tab-button>
        <ion-tab-button tab="quizzes" href="/tabs/quizzes" v-if="loggedIn()">
          <ion-icon :icon="triangle" />
          <ion-label>View quizzes</ion-label>
        </ion-tab-button>
        <ion-tab-button tab="register" href="/tabs/register" v-if="!loggedIn()">
          <ion-icon :icon="square" />
          <ion-label>Register</ion-label>
        </ion-tab-button>
        <ion-tab-button tab="RegisterTeacher" href="/tabs/RegisterTeacher" v-if="!loggedIn()">
          <ion-icon :icon="square" />
          <ion-label>Register T</ion-label>
        </ion-tab-button>
        <ion-tab-button tab="login" href="/tabs/login" v-if="!loggedIn()">
          <ion-icon :icon="square" />
          <ion-label>Login</ion-label>
        </ion-tab-button>
        <ion-tab-button tab="LoginTeacher" href="/tabs/LoginTeacher" v-if="!loggedIn()">
          <ion-icon :icon="square" />
          <ion-label>Login T</ion-label>
        </ion-tab-button>
        <ion-tab-button tab="logout" v-if="loggedIn()" @click="logout()">
          <ion-icon :icon="square" />
          <ion-label>Logout</ion-label>
        </ion-tab-button>
      </ion-tab-bar>
    </ion-tabs>
  </ion-page>
</template>

<script lang="js">
import { IonTabBar, IonTabButton, IonTabs, IonLabel, IonIcon, IonPage } from '@ionic/vue';
import { ellipse, square, triangle } from 'ionicons/icons';
import { mapGetters, mapActions } from 'vuex';
import { useRouter } from 'vue-router';
import { reactive } from '@vue/reactivity';
import axios from 'axios';

export default {
  mounted () {
    this.eventBus.on('login', (args) => {
      this.state.loggedIn = args;
    })
  },
   methods: {
     ...mapActions('login',{
      doLogout:'doLogout'
    }),
    ...mapGetters('login',
      ['loggedIn']
    ),
    logout () {
      this.doLogout();
      this.state.loggedIn = false;
      axios.post(process.env.VUE_APP_TEACHER_API_URL+"/teachermanagement/teachers/logout");
      window.location = '/';
    }
  },
  name: 'Tabs',
  components: { IonLabel, IonTabs, IonTabBar, IonTabButton, IonIcon, IonPage },
  setup() {
    const router = useRouter();

    const checkIfIsLogged = () => {
      const token = window.localStorage.getItem('loggedInJwt');
      if (token) {
        return true
      } else {
        return false
      }
    }

    const state = reactive({
      loggedIn: checkIfIsLogged()
    })

    return {
      ellipse, 
      square, 
      triangle,
      router,
      checkIfIsLogged,
      state,
    }
  },
}
</script>