import { createRouter, createWebHistory } from '@ionic/vue-router';
import { RouteRecordRaw } from 'vue-router';
import Tabs from '../views/Tabs.vue'

const routes: Array<RouteRecordRaw> = [
  {
    path: '/',
    redirect: '/tabs/resources'
  },
  {
    path: '/tabs/',
    component: Tabs,
    children: [
      {
        path: '',
        redirect: '/tabs/resources'
      },
      {
        path: 'Resources',
        component: () => import('@/views/Resources.vue')
      },
      {
        path: 'newresource',
        component: () => import('@/components/cards/CreateResourceCard.vue')
      },
      {
        path: 'resource/:id',
        component: () => import('@/components/cards/ExpandedResourceCard.vue')
      },
      {
        path: 'Register',
        component: () => import('@/views/Register.vue')
      },
      {
        path: 'RegisterTeacher',
        component: () => import('@/views/RegisterTeacher.vue')
      },
      {
        path: 'Login',
        component: () => import('@/views/Login.vue')
      },
      {
        path: 'LoginTeacher',
        component: () => import('@/views/LoginTeacher.vue')
      },
      {
        path: 'Quizzes',
        component: () => import('@/views/Quizzes.vue')
      },
      {
        path: 'Quiz/:id',
        component: () => import('@/views/Quiz.vue')
      },
      {
        path: 'TakeQuiz/:id',
        component: () => import('@/views/TakeQuiz.vue')
      },
    ]
  },
]

const router = createRouter({
  history: createWebHistory(process.env.BASE_URL),
  routes
})

export default router
